import React from 'react';
import { TodoContext } from '../TodoContex';
import './TodoCounter.css';

const TodoCounter = () => {

  const {totalTodos,todosCompletados} = React.useContext(TodoContext)

  return (
    <h2 className="titulo">
      has completado {todosCompletados} de {totalTodos} ToDos
    </h2>
  );
};
export { TodoCounter };
