import React from 'react';
import { useLocalStorage } from './useLocalStorage';

const TodoContext = React.createContext();

function TodoProvider(props) {
  const {
    item: oneTodo,
    saveItem: saveTodos,
    loading,
    error,
  } = useLocalStorage('TODOS_V2', []);

  const [statusValue, setStatus] = React.useState('');
  const [openModal, setOpenModal] = React.useState(false);

  const todosCompletados = oneTodo.filter((Todo) => !!Todo.completed).length;
  const totalTodos = oneTodo.length;

  let searchTodo = [];
  if (!statusValue.length >= 1) {
    searchTodo = oneTodo;
  } else {
    searchTodo = oneTodo.filter((todo) => {
      const textTodo = todo.texto.toLowerCase();
      const textSearch = statusValue.toLowerCase();
      return textTodo.includes(textSearch);
    });
  }

  const addTodo = (texto) => {
    const newTodo = [...oneTodo];
    newTodo.push({
      completed: false,
      texto,
    });
    saveTodos(newTodo);
  };

  const completarTodo = (texto) => {
    const index = oneTodo.findIndex((todo) => todo.texto === texto);
    const newTodo = [...oneTodo];
    newTodo[index].completed = true;
    saveTodos(newTodo);
  };
  //    todos[index]= {
  //      texto: todos[index].texto,
  //      completed: true
  //    };

  const eliminarTodo = (texto) => {
    const index = oneTodo.findIndex((todo) => todo.texto === texto);
    const newTodo = [...oneTodo];
    newTodo.splice(index, 1);
    saveTodos(newTodo);
  };

  return (
    <TodoContext.Provider
      value={{
        loading,
        error,
        totalTodos,
        todosCompletados,
        statusValue,
        setStatus,
        searchTodo,
        addTodo,
        completarTodo,
        eliminarTodo,
        openModal,
        setOpenModal,
      }}
    >
      {props.children}
    </TodoContext.Provider>
  );
}

export { TodoContext, TodoProvider };
