import React from 'react';
import './TodoItems.css';

const TodoItems = (props) => {
  return (
    <li className="tarea">
      <span
        className={`Icon Icon-chek ${
          props.oneCompleted && 'Icon-check-active'
        }`}
        onClick={props.completed}
      >
        <img
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/b/b7/Gtk-ok.svg/1200px-Gtk-ok.svg.png"
          alt="Chulito"
        />
      </span>
      <p
        className={`TodoItem-p ${
          props.oneCompleted ? 'TodoItem-p-complete' : ''
        }`}
      >
        {props.text}
      </p>
      <span onClick={props.delete}>
        <img
          src="https://geoinn.com/wp-content/uploads/2018/08/010_x-3-512.png"
          alt="Equix"
        />
      </span>
    </li>
  );
};

export { TodoItems };
