import React from 'react';
import { TodoContext } from '../TodoContex';
import './TodoSearch .css';

const TodoSearch = () => {
  const { statusValue, setStatus } = React.useContext(TodoContext);

  const onChanges = (event) => {
    setStatus(event.target.value);
    console.log(event.target.value);
  };

  return (
    <input
      className="input"
      placeholder="Cebollita dos"
      value={statusValue}
      onChange={onChanges}
    />
  );
};

export { TodoSearch };
