import React from 'react';
import { TodoContext } from '../TodoContex';
import { TodoCounter } from '../TodoCounter';
import { TodoSearch } from '../TodoSearch';
import { TodoList } from '../TodoList';
import { TodoItems } from '../TodoItems';
import { CreateTodoButton } from '../CreateTodoButton';
import { Modal } from '../Modal';
import { TodoForm } from '../TodoForm';


const AppUI = () => {
  const {
    error,
    loading,
    searchTodo,
    completarTodo,
    eliminarTodo,
    openModal,
    setOpenModal,
  } = React.useContext(TodoContext);

  return (
    <React.Fragment>
      <TodoCounter />
      <TodoSearch />

      <TodoList>
        {error && <p>Hubo un herror al cargar tus datos...!!!</p>}
        {loading && <p>Estamos cargando tus datos ...</p>}
        {!loading && !searchTodo.length && <p>¡ Crea tu primer ToDo !</p>}

        {searchTodo.map((todo) => (
          <TodoItems
            key={todo.texto}
            text={todo.texto}
            oneCompleted={todo.completed}
            completed={() => completarTodo(todo.texto)}
            delete={() => eliminarTodo(todo.texto)}
          />
        ))}
      </TodoList>

      {openModal ? <Modal> <TodoForm/> </Modal> : ''}

      <CreateTodoButton setOpenModal={setOpenModal} openModal={openModal}/>
    </React.Fragment>
  );
};
export { AppUI };
